import React, { useContext, useEffect, useRef, useState } from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components';
import { HeaderContext } from '../store/HeaderContext';
import { BrowserContext } from '../store/BrowserContext';
import { Avatar } from '../StyledComponents';
import { playSound } from '../utils';
import ReactDOM from 'react-dom'


const NotificationItem = styled.div`
  display: flex;
  align-items: center;
  padding: 20px 10px 20px 10px;
  cursor: pointer;
`;

const style1={
    position : 'fixed',
    top: -200,
    zIndex: 3,
    width: '100vw'
}
const style2={
    width: '100vw',
    maxWidth: 700,
    margin: 'auto',
    backgroundColor: 'var(--white)',
    border: '1px solid var(--bs-border-color)',
    borderTop: 'none',
}


const SlidingNotification = ({notification}) => {
    const notificationRef = useRef()
    useEffect(()=>{
        if (notificationRef.current){
            notificationRef.current.style.top = '0'
            setTimeout(transitionToTop, 4000)
            playSound()
        } 

    }, [notificationRef])

    const {setNotifications} = useContext(HeaderContext)
    const {togglePopup} = useContext(BrowserContext)
    const clickHandler =(notification)=>{
        transitionToTop()
        togglePopup(''); 
        setNotifications(notifications=>{
        notification = notifications.find(n => n.id === notification.id);
        if (notification) notification.is_seen = true
            return notifications
        })
    }

    // Make notification responsive to sliding
    const [moveDir, setMoveDir] = useState(null);
    const [startY, setStartY] = useState(0);
    const [startX, setStartX] = useState(0);

    const handleTouchStart = (e) => {
        setStartY(e.touches[0].clientY);
        setStartX(e.touches[0].clientX);
    };

    const handleTouchMove = (e) => {
        notificationRef.current.style.transition = '0s'
        const DX = e.touches[0].clientX - startX 
        const DY =  startY - e.touches[0].clientY
        if (DY > 40 && (-50 < DX < 50)) setMoveDir('UP');
        else if (DX > 50) {
            setMoveDir('RIGHT');
            notificationRef.current.style.transform = `translateX(${DX}px)`
        }
        else if ( DX < -50) {
            setMoveDir('LEFT');
            notificationRef.current.style.transform = `translateX(${DX}px)`
        }
        else {
           setMoveDir(null) 
        }
    };

    const handleTouchEnd = () => {
        notificationRef.current.style.transition = '0.5s'
        if (moveDir == 'UP'){
           transitionToTop()
        } else if (moveDir == 'LEFT'){
            transitionToLeft()
        } else if (moveDir == 'RIGHT'){
            transitionToRight()
        }
    }
    
    const transitionToLeft=()=>{
        notificationRef.current && (notificationRef.current.style.transform = 'translateX(-100vw)')
    }
    const transitionToRight=()=>{
        notificationRef.current && (notificationRef.current.style.transform = 'translateX(100vw)')
    }
    const transitionToTop=()=>{
        notificationRef.current && (notificationRef.current.style.top = '-200px')
    }
  return  ReactDOM.createPortal(
    <div style={style1} ref={notificationRef} onTouchStart={handleTouchStart} onTouchMove={handleTouchMove} onTouchEnd={handleTouchEnd}>
        <div style={style2}>
            <Link key={notification.id} className='d-block' to={'/redirect?redirect='+ notification.url} onClick={()=>clickHandler(notification)}>
                <NotificationItem >
                    <Avatar src={notification.avatar} alt={notification.name} />
                    <div className='flex-grow-1 d-flex flex-column'>
                        <strong>{ notification.notifier_name.text || notification.notifier_name} {notification.notifier_name.is_verified && <img width='16' src='/static/others/verified.png'/>}</strong>
                        <p className='mb-0 small'>{notification.text}</p>
                    </div>
                </NotificationItem>
            </Link>
        </div>
    </div>
  , document.getElementsByTagName('body')[0])
}

export default SlidingNotification